import React,{useState} from 'react';

const DummyImage='https://storage.googleapis.com/prepladder-images/medical/X3gkBKNLpIzzoRu2H9mF1580130231.png';

const Img = ({src,alt})=>{
    const [brokenImg,setBrokenImg]=useState(0);
    return (
      <img src={brokenImg ? DummyImage: src}  onError={()=>setBrokenImg(1)} alt={src} />
    )
}

export default Img;
