import React,{useState} from "react";
import {formatMoney} from '../common/Utils';
import {InfoIcon} from '../common/Icons';
import Popup from "reactjs-popup";

function PriceDetails({subTotal,shippingCharges,payableAmount,couponDiscount,couponStatus,couponMessage,walletCashUsed,effectivePrice,walletCashAvailable,refreshApi,useCashback,fromOrders,infoBtnText}) {

  const [checked,setCheck]=useState(walletCashUsed ? true : false);

  const onChangeCashBack=()=>{
    refreshApi(checked ? 0 : 1)
    // console.log(checked);
    setCheck(!checked)
  }

  return (
    <div className="price-details mT30">
      <h2 className="text-uppercase f-18 mB20">price details</h2>
      <ul className="list-block f-14">
        <li>
          Sub Total<span className="float-right">Rs. {formatMoney(subTotal)}</span>
        </li>
        <li>
          Shipping Charges <span className="float-right">Rs. {formatMoney(shippingCharges)}</span>
        </li>
        <li>
          Amount Payable <span className="float-right">Rs. {formatMoney(payableAmount)}</span>
        </li>
        {
          couponStatus
          ?
          <li>
            Coupon Discount <span className="float-right">Rs. {formatMoney(couponDiscount)}</span>
          </li>
        : null
        }

        {
          fromOrders
          ?
          <li>
          Wallet Cash <span className="float-right">- Rs. { formatMoney(walletCashUsed)}</span>
          {/* <input type="checkbox" checked={checked} onChange={onChangeCashBack} /> */}
          </li>
          : 
          <React.Fragment>
            <li>
              Wallet Cash <span className="float-right">- Rs. { formatMoney(walletCashUsed)}
              {
                infoBtnText
                ?  <Popup
                trigger={<img src={InfoIcon} height='16px' alt="info icon"/>}
                position="bottom right"
                on="hover"
                closeOnDocumentClick
              >
                <span> {infoBtnText} </span>
              </Popup>
              : null
              }
              </span>
              <input type="checkbox" checked={checked} onChange={onChangeCashBack} />
            </li>
            <li>
              &nbsp;   <span className="float-right"> 
              ( Remaining Cashback Rs. { formatMoney(walletCashAvailable)})
              </span>
            </li>
          </React.Fragment>
        }
       

        <li>
          Effective Price after Cashback
          <span className="float-right">Rs. {formatMoney(effectivePrice)}</span>
        </li>
        
      </ul>
    </div>
  );
}

export default PriceDetails;
