import React,{useState} from "react";
import Link from "../common/Link";
import {withContext} from '../contexts/Context';
import Fetch from '../common/Fetch';
import {isUserExist} from '../common/Auth';
import {withRouter} from 'react-router-dom';
import Img from './Img';
// import EncodeUrl from '../common/UrlName';

const BookCard = function({proId, name, author, price, cashback, img,style,to ,context,isBookmarked,history,sellingPrice}) {

  const {addItemToCart,cart,toggleLoginPopup}=context;
  const [bookmarkedLocally,setbookmark]=useState(isBookmarked)
  const onBookmark= id =>{
    Fetch('productBookmark',{productID:id}).then(d=>{
      // console.log(d);
      if(d.status){
        setbookmark(!bookmarkedLocally);
      }
    })
  }
  const pathTo=pathTo=>history.push(pathTo)
  
  return (
    <div className="book-col" style={style}>
      {
        !Number(proId)
        ?
        <div className="book-outofstock">
        Out of stock
      </div>
      : null

      }
     
      <div className="book__image" onClick={ Number(proId) ? ()=>pathTo(to) : null}>
        <Link>
          <Img src={img} alt={name}/>
        </Link>
      </div>
      <div className="book_info">
        <div className="book-category-detail">
          <div className="book-name f-15">
            {name}
            {/* <a href="">
              <i class="sprite-img bookmark-icon"></i>
            </a> */}
          </div>
          <div className="book-writer text-light f-14">{author}</div>
          <div className="book-price">
            <span className="f-15 mR5">Rs. {sellingPrice}</span>
            <span className="text-red f-13">{Number(proId) ? `(${cashback} Cashback)` : `( Out of stock )`}</span>
          </div>
        </div>
        <div className="book-category-hoverBox">
          <div className="btn-group">
            {
              Number(proId)
              ?
              <React.Fragment>
                {/* <button  className="btn btn-border btn-radius mR5" onClick={!isUserExist() ? ()=>toggleLoginPopup(true) :()=>onBookmark(proId)}>
                {bookmarkedLocally ? "UNBOOKMARK": "BOOKMARK"} 
                </button> */}
                <button onClick={ !cart[proId] ? ()=>addItemToCart(proId) : !isUserExist() ? ()=>pathTo(`/login?referer=/cart`)  : ()=> pathTo(`/cart`) } className="btn btn-gradient btn-radius">
                  {cart[proId] ? "GO TO CART" : "ADD TO CART"}
                </button>
              </React.Fragment>
              : null
            }
           
          </div>
          <div className="availability">
            <span className="f-14">Availability:</span>
            <span className="f-16 f-sm-15" style={ {color:!Number(proId) ? 'red' :''}}> {Number(proId) ? 'In Stock' :'Out of stock'}</span>
          </div>
          <div className="book-price">
            <strong className="f-16">Rs. {sellingPrice}</strong>
          </div>
        </div>
      </div>
    </div>
  );
};

export default withContext(withRouter(BookCard));

