import React, { Component } from 'react';
import Fetch from '../common/Fetch';
import PopUp from '../components/Popup';
import Loader from '../components/Loader';
import DateAndTime from 'date-and-time';


class Tracking extends Component {
    state={
        msg:'',
        isLoading:true,
        events:[]
    }

    componentDidMount(){
        this.setState({isLoading:true});
        const {trackID}=this.props;

       trackID && trackID.length && Fetch('trackShipment',{trackingNumber:trackID}).then(d=>{
            // console.log(d);
            if(d.status){
                this.setState({msg:d.message,events:d.data.Events,isLoading:false})
            }else{
                this.setState({msg:d.message,isLoading:false})
            }
        })
    }

    render() {
        const {isLoading,events,msg}=this.state;
        const {onClose}=this.props;

        return (
            <PopUp onClose={onClose}>
                <div className='tracking'>
                    {
                        isLoading
                        ?
                        <Loader/>
                        :
                        <React.Fragment>
                            <div className="float-right can-click" style={{fontSize:'2rem'}} onClick={onClose}>✕</div>
                            <div className="text-center tracking__head">Order Information</div>
                            <table>
                                    <tr>
                                        <th>Date</th>
                                        <th>Time</th>
                                        <th>Location</th>
                                        <th>Status</th>
                                    </tr>
                                    {
                                        events && events.length
                                        ?
                                        events.map((d,i)=>(
                                            <tr>
                                                <td>{`${DateAndTime.format(new Date(d.Timestamp),'ddd, MMM DD')}`}</td>
                                                <td>{`${DateAndTime.format(new Date(d.Timestamp),'hh:mm A')}`}</td>
                                                <td>{d.Address.City}, {d.ArrivalLocation}</td>
                                                <td>{d.EventDescription}</td>
                                            </tr>
                                        ))
                                        :null
                                    }
                                    
                            </table>
                            <p className='text-center'>{msg}</p>
                        </React.Fragment>
                    }
                </div>
            </PopUp>
        );
    }
}

export default Tracking;